.weather{
    padding-bottom: 40px;
    font-size: 14px;
    color: #fff;
    background-color:#edf2f6;
    z-index: -12;
    display: flex;
    flex-direction: column;
}
.weather__header{

    height: 400px;
    padding-bottom: 20px;
    position: relative;
}
.weather__headerOverlay{
    width: 100%;
    z-index: 2;
    height: 100%;


    position: relative;
}
.weather__headerBackground{
    background-image: url('https://html.crumina.net/html-olympus/img/top-header6.png');
    background-position: 0 0;
    background-repeat: repeat-x;
    animation: animatedBackground 50s linear infinite ;
    -webkit-animation: animatedBackground 50s linear infinite;
    will-change: background-position;
    height: 100%;
    width: 100%;
    z-index: 1;
    position: absolute;
    top: 0;
}

@keyframes animatedBackground {
    from { background-position: 0 100%; }
    to { background-position: -4000px 100%; }
}
.weather__headerContent{
    display: flex;
    justify-content: space-between;
    height: 100%;
    box-sizing: border-box;
    padding-top:70px;
    background-image: url('https://html.crumina.net/html-olympus/img/weather-bottom.png');
    background-repeat: no-repeat;
    background-position: center center;
    flex-direction: column;
    align-items: center;

}
.weather__headerContent > h1{
    font-size: 60px;
    display: flex;

}
.weather__headerContentCaption{
    color: #fff;

}
.weather__headerContentCaptionSup{
    font-size: 40px;
    align-self: flex-start;
    align-content: flex-start;

}

.weather__headerContent >hgroup{
    display: flex;
    justify-content: space-around;
    width: 150px;
}
.weather__headerDesc{
    font-size: 24px;
    text-transform: capitalize;
    font-weight: bold;
    margin-bottom: 0;
}
.weather__headerParam{
    display: flex;
    width: 250px;
    justify-content: space-between;
    padding: 0;

}
.weather__headerParam > li{
    list-style: none;
    font-size: 18px;
    display: flex;
    flex-direction: column;

}
.fas{
    padding-bottom: 15px;
}
.weather__container{
    width: 80%;
    padding-top: 30px;
    display: grid;
    align-self: center;
    grid-template-columns: 300px 1fr;
    grid-template-rows: 1fr;

   
}

.weather__settings{
   width: 900px;

    background-color: #fff;
    margin-left: 50px;
    height: 200px;
    margin-bottom: 20px;

}
.weather__settingsTitle{
    height: 50px;
    padding:20px 25px;
    box-sizing: border-box;
    background-color: #fff;
    border-bottom: 1px solid lightgray;

}
.weather__settingsContent{
    width: 100%;
    justify-content: space-between;
    display: flex;
    align-items: center;
    height: 100px;
    padding: 0 20px;
    box-sizing: border-box;
}
.weather__settingsSwitch{
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-width: 40%;
    margin-top: 20px;
}
.weather__extendedForecastItem{
    display: flex;
    flex-direction: column;
    border-right: 1px solid lightgray;
    align-items: center;
    justify-content: center;
    width: 15%;
    padding: 10px 0;

}
.weather__extendedForecastItem:last-child{
    border-right: none;
}
.weather__extendedForecastItem >hgroup{
    display: flex;
    justify-content: space-between;
    width: 40px;
}
.weather__extendedForecastItem>img{
    width: 50px;
}
.weather__extendedForecast{
    display: flex;
    padding: 0;
    list-style: none;
    border-bottom: 1px solid lightgray;
    margin-top: 0;

}
@media all and (max-width: 1460px){
    .weather__container{
     grid-template-columns: 1fr;


    }
    .weather__rightSide{
        order:-1;
    }
    .weather__settings{
        margin-left: 0px;
        width: 100%;
    }
    .weather__extendedForecastItem > h3{
        font-size: 13px;
    }
}
@media all and (max-width: 890px){
    .weather__extendedForecast{
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-rows: 1fr 1fr 1fr;
    }
    .weather__settings-mobile{
        height: 405px;
    }
    .weather__extendedForecastItem{
        width: 100%;
        border-right: none;
    }
    .weather__settingsContent{
        flex-direction: column;
    }
}
@media all and (max-width: 560px){
    .weather__extendedForecast{

        grid-template-columns: 1fr 1fr;

    }

    .weather__settings-mobile{
        height: 524px;
    }
    .weather__container{
        width: 95%;
        box-sizing: border-box;
    }

}
